
// dev
import React, { useState } from "react";
import PropTypes from "prop-types";
import _ from "lodash";
import { makeStyles } from "@material-ui/styles";

// app


//
// Settings
//

const useStyles = makeStyles({
	root: {
		flexGrow: 1, padding: "2rem 0",
		display: "flex", flexDirection: "column",
		"& *": {
			animation: "$fadein 150ms",
			willChange: "opacity",
		},
	},
	input: {
		display: "flex",
		alignItems: "center",
		marginBottom: "1rem",
		flexWrap: "wrap",
		"& > *": {
			margin: "0 0.5rem", padding: 0,
			maxWidth: "50vw",
		},
	},
	"@keyframes fadein": {
		from: {
			opacity: 0,
		},
		to: {
			opacity: 1,
		},
	},
});

export default function Settings ({ state, dispatch }) {
	const { duration, useDefaultStretches, stretchesMap } = state;
	const classes = useStyles();

	const [ newStretch, setNewStretch ] = useState({});
	
	return (
		<div className={ classes.root }>
			<h4>Settings:</h4>
			<div className={ classes.input }>
				<h5>Duration:</h5>
				<InputText 
					value={ duration }
					onChange={ value => dispatch({ type: "setDuration", value: Number( value ) })}
				/>
				<p>seconds &asymp; { _.round( duration / 60, 1 ) } mins</p>
			</div>
			<div className={ classes.input }>
				<h5>Use Default Stretches?:</h5>
				<Toggle 
					checked={ useDefaultStretches } 
					onChange={ () => dispatch({ type: "setUseDefaultStretches", value: !useDefaultStretches }) }
				/>
			</div>
			{ !useDefaultStretches && <>
				{ !_.isEmpty( stretchesMap ) && _.map( _.concat( stretchesMap ), ( stretch, i ) => (
					<div className={ classes.input }>
						<InputText
							label="Name"
							value={ _.get( stretch, "name" ) }
							onChange={ name => dispatch({ 
								type: "updateStretchesMap", 
								stretchesMap: _.map( stretchesMap, ( el, j ) => i != j ? el : { ...el, name }), 
							})} 
						/>
						<InputText 
							label="Frequency"
							value={ _.get( stretch, "frequency" ) }
							onChange={ frequency => dispatch({ 
								type: "updateStretchesMap", 
								stretchesMap: _.map( stretchesMap, ( el, j ) => i != j ? el : { ...el, frequency }), 
							})} 
						/>
						<Toggle 
							label="Custom?"
							value={ _.get( stretch, "custom" )}
							onChange={ () => dispatch({ 
								type: "updateStretchesMap", 
								stretchesMap: _.map( stretchesMap, ( el, j ) => i != j ? el : { ...el, custom: !_.get( stretch, "custom" ) }), 
							})}
						/>
					</div>
				))}
				<div className={ classes.input }>
					<InputText 
						value={ _.get( newStretch, "name" ) }
						onChange={ name => setNewStretch( ns => ({ ...ns, name }))} 
					/>
				</div>
			</> }
		</div>
	);
}
Settings.propTypes = {
	state: PropTypes.object,
	dispatch: PropTypes.func,
};


// Toggle Component
const useToggleStyles = makeStyles({
	root: {
		display: "flex",
		alignItems: "center",
	},
	label: {
		fontSize: "85%",
		padding: "0 1rem 0 0",
		margin: 0,
	},
	toggle: {
		borderStyle: "solid",
		borderColor: "var(--tertiary)",
		borderWidth: "1px",
		padding: "3px",
		"& span": {
			opacity: ({ checked }) => checked ? 1 : 0,
			fontWeight: "bold",
		},
	},
});

const Toggle = ({ checked, onChange, label }) => {
	const classes = useToggleStyles({ checked });

	return (
		<div className={ classes.root }>
			{ label && <p className={ classes.label }>{ label }</p>}
			<span className={ classes.toggle } onClick={ onChange }>
				<span className="fa-check" />
			</span>
		</div>
	);
};
Toggle.propTypes = {
	label: PropTypes.string,
	checked: PropTypes.bool,
	onChange: PropTypes.func,
};


// Input Text Component
const useInputTextStyles = makeStyles({
	root: {
		display: "flex",
		alignItems: "center",
	},
	label: {
		fontSize: "85%",
		padding: "0 1rem 0 0",
		margin: 0,
	},
	input: {
		borderStyle: "solid",
		borderColor: "var(--tertiary)",
		borderWidth: "1px",
		padding: "2px",
		width: ({ isTypeNumber }) => isTypeNumber ? "3rem" : "8rem",
		"& :active, :hover, :focus": {
			backgroundColor: "var(--secondary)",
		},
		"& input": {
			border: "none",
		},
	},
});

const InputText = ({ value, onChange, label }) => {
	const isTypeNumber = typeof value === "number";
	const classes = useInputTextStyles({ isTypeNumber });

	return (
		<span className={ classes.root }>
			{ label && <label className={ classes.label }>{ label }:</label> }
			<input
				className={ classes.input }
				type={ isTypeNumber ? "number" : "text" } 
				value={ value } 
				onChange={ e => onChange( e.target.value ) } 
			/>
		</span>
	);
};
InputText.propTypes = {
	label: PropTypes.string,
	value: PropTypes.oneOfType([
		PropTypes.number,
		PropTypes.string,
	]),
	onChange: PropTypes.func,
};
